<template>
  <div class="text-center border border-black py-5 px-3 mb-5" id="apiTip">
    <h2 class="mb-3">Daily tip</h2>
    <p v-if="advice">{{ advice }}</p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ApiTips",
  data() {
    return {
      advice: null
    };
  },
  created() {
    this.getAdvice();
  },
  methods: {
    async getAdvice() {
      try {
        const response = await axios.get("https://api.adviceslip.com/advice");
        this.advice = response.data.slip.advice;
      } catch (error) {
        console.error(error);
      }
    }
  }
};
</script>
